@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;600&display=swap");

* {
    padding: 0;
    margin: 0;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}
a {
    text-decoration: none;
    color: #fff;
}
body {
    font-family: "Poppins", sans-serif;
}

.container {
    max-width: 70rem;
    padding: 0 2rem;
    margin: 0 auto;
}

header {
    width: 100%;
    height: 100vh;
    position: relative;
    overflow: hidden;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
}
.particle-js {
    height: 100%;
    width: 100%;
    position: fixed;
    z-index: -100;
    background-color: #333;
    background-size: cover;
    background-position: 50% 50%;
    background-repeat: no-repeat;
}
canvas {
    width: 100%;
    height: 100vh;
    display: block;
    vertical-align: bottom;
}


header:after {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    background-color: rgba(0, 0, 0, 0.05);
    z-index: 25;
}

.bg-main {
    position: absolute;
    top: 0px;
    width: 2100px;
    right: 0;
    opacity: 1;
}

nav {

    top: 0;
    left: 0;
    width: 100%;
    padding: 1rem 0;
    z-index: 30;
    background: -webkit-gradient(linear, left top, left bottom, from(rgba(0, 0, 0, 0.07)), to(transparent));
    background: -webkit-linear-gradient(top, rgba(0, 0, 0, 0.07), transparent);
    background: -o-linear-gradient(top, rgba(0, 0, 0, 0.07), transparent);
    background: linear-gradient(to bottom, rgba(0, 0, 0, 0.07), transparent);
    position: fixed;
}

nav .container {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-align: center;
        align-items: center;
}

.logo {
    width: 140px;
    color: #fff;
    font-size: 1.1rem;
    text-transform: uppercase;
    font-weight: 600;
    letter-spacing: 2px;
}

.hamburger-menu {
    background: rgba(255, 255, 255, 0);
    border: 1px white solid;
    color: #fff;
    width: 5.55rem;
    height: 2.9rem;
    text-align: center;
    cursor: pointer;
    font-weight: 600;
    font-size: 0.9rem;
    -ms-flex-align: center;
        align-items: center;
}

.bar {
    position: relative;
    width: 1.2rem;
    height: 3px;
    background-color: #fff;
    border-radius: 2px;
}

.bar:before,
.bar:after {
    content: "";
    position: absolute;
    width: 1.55rem;
    height: 3px;
    right: 0;
    background-color: #fff;
    border-radius: 2px;
}

.bar:before {
    -webkit-transform: translateY(-8px);
        -ms-transform: translateY(-8px);
            transform: translateY(-8px);
}

.bar:after {
    -webkit-transform: translateY(8px);
        -ms-transform: translateY(8px);
            transform: translateY(8px);
}

.big-title {
    position: absolute;
    z-index: 30;
    top: calc(40% - 2rem);
    width: 40%;
    -ms-flex-item-align: center;
        align-self: center;
}
.body-start{
    padding-top: 2rem;
}
section {
    width: 100vw;
    background-color: #26272B;
    position: relative;
}

section .container {
    margin-top: 30px;
    width: 80vw;
    padding: 1rem;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    min-height: 60vh;
    border-radius: 40px;
    background: white;
    -webkit-box-shadow: 5px 5px 30px 7px rgba(0,0,0,0.25), -5px -5px 30px 7px rgba(0,0,0,0.22);
            box-shadow: 5px 5px 30px 7px rgba(0,0,0,0.25), -5px -5px 30px 7px rgba(0,0,0,0.22);
}

section img {
    -ms-flex-item-align: center;
        align-self: center;
    width: 90%;
}

section .container > * {
    margin: 2rem;
}

.content {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    color: #000;
    text-align: center;
}


.imgContainer {
    width: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-item-align: center;
        align-self: center;
    -ms-flex-pack: center;
        justify-content: center;
}

.title {
    font-weight: 600;
    font-size: 1.5rem;
    margin-bottom: 1rem;
    padding-bottom: 0.5rem;
    position: relative;
}

.border {
    position: absolute;
    width: 100%;
    height: 3px;
    background-color: #000;
    bottom: 0;
    left: 0;
}

.shadow {
    position: absolute;
    bottom: 100%;
    height: 300px;
    width: 100%;
    left: 0;
    z-index: 20;
    background: -webkit-gradient(linear, left bottom, left top, from(#161616), to(transparent));
    background: -webkit-linear-gradient(bottom, #161616, transparent);
    background: -o-linear-gradient(bottom, #161616, transparent);
    background: linear-gradient(to top, #161616, transparent);
}

/*.opacity {*/
/*  opacity: 0;*/
/*}*/

/*register  us segment start*/
.register {
    height: 100%;
    background: #26272B;
    color: #fff;
    padding: 8px 5px;
}
.register .container{
    width: 95vw;
}
.register .container .title {
    letter-spacing: 1px;
    font-size: 2rem;
    margin-top: 20px;
    margin-bottom: 25px;
    color: black;
}
.register .container .form-container {
    margin-top: 0;
    border-top: 0;
    padding: 10px 50px;
}

.register .container .form-container label {
    padding-left: 10px;
    margin: 1px;
    text-align: center;
    color: black;
}
.register .container .form-container input {
    margin-bottom: 15px;
    padding: 14px 40px;
    font-weight: 400;
    font-size: 17px;
    outline: 0;
    background: rgba(255, 255, 255, 0);
    color: #000;
    border: 1px black solid;
}
.register .container .form-container input:focus {
    border: 3px black solid;
}

.buttonRegister {
    -ms-flex-item-align: center;
        align-self: center;
    margin-top: 1rem;
    text-decoration: none;
    color: rgb(255, 255, 255);
    background: #000;
    padding: 14px 35px;
    border: none;
    border-radius: 16px;
    font-weight: normal;
    text-transform: uppercase;
}

.buttonRegister:hover {
    color: rgb(255, 255, 255);
    -webkit-box-shadow: 0 5px 25px rgba(68, 68, 68, 0.4);
            box-shadow: 0 5px 25px rgba(68, 68, 68, 0.4);
}

div.title {
    margin-bottom: 0;
    text-align: center;
}

/* Register part end */

/* Footer */

.form-label {
    font-size: 1.6rem;
    font-weight: 400;
    text-align: center;
}
.label-radio {
    font-size: 0.9rem;
    font-weight: 200;
}

.bottom-line {
    position: relative;
    height: 2px;
    background: white;
    margin-bottom: 1rem;
    width: 100%;
}

/* Info 2 Class */
.info-2 {
    background: rgb(39, 39, 39);
}
.discord .title {
    padding: 10px;
    color: #fff;
}
.discord {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: center;
        justify-content: center;
    background: #fff;
}
iframe {
    -ms-flex-item-align: center;
        align-self: center;
    padding-bottom: 30px;
}

/* Footer Section */

.site-footer {
    background-color: #26272b;
    padding: 45px 0 20px;
    font-size: 15px;
    line-height: 24px;
    color: #737373;
}
.site-footer hr {
    border-top-color: #bbb;
    opacity: 0.5;
}
.site-footer hr.small {
    margin: 20px 0;
}
.site-footer h6 {
    color: #fff;
    font-size: 16px;
    text-transform: uppercase;
    margin-top: 5px;
    letter-spacing: 2px;
}
.site-footer a {
    color: #737373;
}
.site-footer a:hover {
    color: #3366cc;
    text-decoration: none;
}
.footer-links {
    padding-left: 0;
    list-style: none;
}
.footer-links li {
    display: block;
}
.footer-links a {
    color: #737373;
}
.footer-links a:active,
.footer-links a:focus,
.footer-links a:hover {
    color: #3366cc;
    text-decoration: none;
}
.footer-links.inline li {
    display: inline-block;
}
.site-footer .social-icons {
    text-align: right;
}
.site-footer .social-icons a {
    width: 40px;
    height: 40px;
    line-height: 40px;
    margin-left: 6px;
    margin-right: 0;
    border-radius: 100%;
    background-color: #33353d;
}
.copyright-text {
    margin: 0;
}
@media (max-width: 991px) {
    .site-footer [class^="col-"] {
        margin-bottom: 30px;
    }
}
@media (max-width: 767px) {
    .site-footer {
        padding-bottom: 0;
    }
    .site-footer .copyright-text,
    .site-footer .social-icons {
        text-align: center;
    }
}
.social-icons {
    padding-left: 0;
    margin-bottom: 0;
    list-style: none;
}
.social-icons li {
    display: inline-block;
    margin-bottom: 4px;
}
.social-icons li.title {
    margin-right: 15px;
    text-transform: uppercase;
    color: #96a2b2;
    font-weight: 700;
    font-size: 13px;
}
.social-icons a {
    background-color: #eceeef;
    color: #818a91;
    font-size: 16px;
    display: inline-block;
    line-height: 44px;
    width: 44px;
    height: 44px;
    text-align: center;
    margin-right: 8px;
    border-radius: 100%;
    -webkit-transition: all 0.2s linear;
    -o-transition: all 0.2s linear;
    transition: all 0.2s linear;
}
.social-icons a:active,
.social-icons a:focus,
.social-icons a:hover {
    color: #fff;
    background-color: #29aafe;
}
.social-icons.size-sm a {
    line-height: 34px;
    height: 34px;
    width: 34px;
    font-size: 14px;
}
.social-icons a.facebook:hover {
    background-color: #3b5998;
}
.social-icons a.twitter:hover {
    background-color: #00aced;
}
.social-icons a.linkedin:hover {
    background-color: #007bb6;
}
.social-icons a.dribbble:hover {
    background-color: #ea4c89;
}
@media (max-width: 767px) {
    .social-icons li.title {
        display: block;
        margin-right: 0;
        font-weight: 600;
    }
}

@media (max-width: 850px) {
    section .container {
        grid-template-columns: 1fr;
    }
    .register .container .title {
        font-size: 2rem;
    }
}

@media (max-width: 600px) {
    .big-title {
        top: calc(40% - 2rem);
        width: 70vw;
        left: 15%;
    }
    .bg-main{
        height: 100vh;
        width: 1500px;
        left: -80%;
    }

    .text {
        font-size: 0.8rem;
    }

    .title {
        font-size: 1.2rem;
    }
    .register .container .title {
        font-size: 2rem;
    }
    section .container > * {
        margin: 0;
    }
    .register .container .title {
        text-align: center;
        font-size: 2rem;
    }
    .particle-js {
        height: 100vh !important;
        width: 100vw;
        position: fixed;
        top: 0;
        left: 0;
        z-index: -100;
        background-color: #333;
        background-image: url("");
        background-size: cover;
        background-position: 50% 50%;
        background-repeat: no-repeat;
    }
    canvas {
        width: 100%;
        height: 100vh;
        display: block;

    }
    .tsparticles-canvas-el {
        height: 100vh;
    }
}

pre {
    display: none;
}



